<template>
  <main id="content">
    <pm-sidebar />
    <PmListProducts :windowWidth="windowWidth" v-if="$route.name === 'ProductsCategory'" />
    <PmHomeProducts :windowWidth="windowWidth" v-else-if="$route.name === 'Products'" />
  </main>
</template>

<script>
import PmSidebar from "@/components/layout/Sidebar.vue";
import PmListProducts from "@/components/layout/ListProducts.vue";
import PmHomeProducts from "@/components/layout/HomeProducts.vue";

export default {
  name: "Products",
  components: {
    PmSidebar,
    PmListProducts,
    PmHomeProducts
  },
  data() {
    return {
      windowWidth: window.innerWidth
    };
  },
  mounted() {
    window.addEventListener("resize", this.handleWindowResize);
  },
  methods: {
    handleWindowResize() {
      this.windowWidth = window.innerWidth;
    },
    responsive() {}
  },
  watch: {
    windowWidth: "handleWindowResize"
  }
};
</script>

